<template>
  <div id='cy'>
    <div id='topology-breadcrumb'>
      <ol class='breadcrumb'>
        <li id='li-topology'><a href='#'>Topology</a></li>
        <li></li>
      </ol>
    </div>
    <button id='topology-randomize' class="btn btn-sm btn-default">
      <span class="glyphicon glyphicon-certificate"></span>
    </button>
    <div id='topology-toolbar' class='btn-group' data-toggle='buttons'>
      <label id='topology-toolbar-link' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='link' checked> Link
      </label>
      <label id='topology-toolbar-tunnel' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='tunnels' checked> Tunnel
      </label>
      <!-- <label @click='toggleEdgeFilter' class='btn btn-default deactive btn-sm' data-toggle="tab" disabled>
              <input type='checkbox' value='pw'> PW
            </label> -->
    </div>
    <div id='topology-lsp-toolbar' class='btn-group' data-toggle='buttons'>
      <label id='topology-lsp-wk-btn' class='btn btn-default active btn-sm'>
        <input type='checkbox' value='lsp_wk' checked> Working
      </label>
      <label id='topology-lsp-pt-btn'class='btn btn-default active btn-sm'>
        <input type='checkbox' value='lsp_pt' checked> Protection
      </label>
    </div>
    <!-- <div id='topology-chosen'>
        <select data-placeholder='Select link or tunnel to highlight' class='chosen-select-deselect'>
          <option value=''></option>
          <optgroup label='Links (12)'>
            <option>Dallas Cowboys</option>
            <option>New York Giants</option>
            <option>Philadelphia Eagles</option>
            <option>Washington Redskins</option>
          </optgroup>
          <optgroup label='Tunnels (20)'>
            <option>Chicago Bears</option>
            <option>Detroit Lions</option>
            <option>Green Bay Packers</option>
            <option>Minnesota Vikings</option>
          </optgroup>
          <optgroup label='Lsps (123)'>
            <option>Chicago Bears</option>
            <option>Detroit Lions</option>
            <option>Green Bay Packers</option>
            <option>Minnesota Vikings</option>
          </optgroup>
        </select>
      </div> -->
  </div>
</template>

<script>
import store from '../vuex/store'
import { mapGetters } from 'vuex'
import 'expose-loader?$!expose-loader?jQuery!jquery'
import cytoscape from 'cytoscape'
import panzoom from 'cytoscape-panzoom'
import autopanOnDrag from 'cytoscape-autopan-on-drag'
import contextMenus from 'cytoscape-context-menus'
import navigator from 'cytoscape-navigator'
import qtip from 'cytoscape-qtip'
import EventBus from './event-bus'
import coseBilkent from 'cytoscape-cose-bilkent'
import { HTTP } from '../http-common'

export default {
  mounted() {
    panzoom(cytoscape);
    autopanOnDrag(cytoscape);
    navigator(cytoscape);
    contextMenus(cytoscape, $);
    qtip(cytoscape);
    coseBilkent(cytoscape);

    this.init();

    store.subscribe((mutation, state) => {
      this.onUpdate(mutation.payload);
    });

    // $('.chosen-select').chosen();
    // $('.chosen-select-deselect').chosen({ allow_single_deselect: true });

    // $('#topology-chosen').on('mousewheel', (event) => {
    //   if (event.originalEvent.wheelDelta > 0 || event.originalEvent.detail < 0) {
    //     console.log('up');
    //   } else {
    //     console.log('down');
    //   }

    //   event.preventDefault();
    //   event.stopPropagation();
    //   event.stopImmediatePropagation();
    // });

    EventBus.$on('GRID_RESIZE', (width, height) => {
      $('.cytoscape-navigator').css('top', (height - 205) + 'px');
      this.cy.resize();
    });

    EventBus.$on('GRID_ITEM_SELECTED', (selectedItem) => {
      if (this.itemAnimationTimerId) {
        clearInterval(this.itemAnimationTimerId);
      }

      // if (this.edgesForPathDisplay.length > 0) {
      //   this.removeEdgesForPathDisplay();
      // }

      if (selectedItem.item.type === 'node') {
        let node = this.cy.filter(`node[id='${selectedItem.item.id}']`);
        this.itemAnimationTimerId = setInterval(() => {
          node.animate({ style: { 'border-color': '#3F89DF', 'border-width': 8, 'border-opacity': 0.2 } }, { duration: 400 })
            .animate({ style: { 'border-color': '#455A64', 'border-width': 1, 'border-opacity': 1 } }, { duration: 400 });
        }, 800);
      }

      if (selectedItem.item.type === 'link') {
        let edge = this.cy.filter(`edge[id='${selectedItem.item.id}']`);
        this.itemAnimationTimerId = setInterval(() => {
          edge.animate({ style: { 'width': 6 } }, { duration: 400 })
            .animate({ style: { 'width': 1 } }, { duration: 400 });
        }, 800);
      }

      if (selectedItem.item.id.indexOf('TUNNELTRAIL') > -1) {
        this.updatedBreadcrumb('lsp');
        this.selectedTunnelId = selectedItem.item.id;
        this.flagLspDisplayMode = true;
        this.onUpdate(store.state.elements);
      }
    });
    // cy.on('ready', function() {
    //   updateBounds();
    // });
    // //if they resize the window, resize the diagram
    // $(window).resize(function() {
    //   updateBounds();
    // });

    // var updateBounds = function() {
    //   var bounds = cy.elements().boundingBox();
    //   $('#cyContainer').css('height', bounds.h + 300);
    //   cy.center();
    //   cy.resize();
    //   //fix the Edgehandles
    //   $('#cy').cytoscapeEdgehandles('resize');
    // };
  },
  data() {
    return {
      initLoad: false,
      showPortLabel: false,
      labelFormat: (item) => { return item.name },
      nodesPos: [],
      nodeIcons: new Map([['91', 'node_icon_b'], ['92', 'node_icon_m'], ['93', 'node_icon_m'], ['94', 'node_icon_m'], ['95', 'node_icon_a'], ['V', 'node_icon_v']]),
      itemAnimationTimerId: 0,
      selectedTunnelId: '',
      flagLspDisplayMode: false,
      topologyLayout: {
        name: 'cose-bilkent',
        nodeDimensionsIncludeLabels: true,
        animate: 'end',
        animationEasing: 'ease-out',
        animationDuration: 500,
        padding: 50,
        tilingPaddingHorizontal: 30,
        randomize: true
      }
      // edgesForPathDisplay: []
    }
  },
  methods: {
    // removeEdgesForPathDisplay() {
    //   if (this.edgesForPathDisplay.length > 0) {
    //     this.cy.batch(() => {
    //       this.edgesForPathDisplay.forEach((element) => {
    //         // console.log(element);
    //         // if (element.group === 'nodes' || element.classes === 'link') {
    //         //   return;
    //         // }

    //         let edge = this.cy.filter(`edge[id = "${element.data.id}"]`);
    //         if (edge.length > 0) {
    //           this.cy.remove(edge);
    //         }
    //       }, this);

    //       this.cy.nodes().connectedEdges().forEach((ele) => { ele.show(); });
    //       this.edgesForPathDisplay = [];
    //     });
    //   }
    // },
    init() {
      let cy = this.cy = cytoscape(
        {
          container: document.getElementById('cy'),
          boxSelectionEnabled: true,
          autounselectify: false,
          wheelSensitivity: 0.1,
          elements: [],
          style: [ // the stylesheet for the graph
            {
              selector: 'node',
              style: {
                // 'shape': 'rectangle',
                'border-width': 1,
                'label': 'data(label)',
                'background-fit': 'contain',
                'background-image-opacity': 1,
                // 'text-valign': 'center',
                // 'text-outline-width': 2,
                // 'text-outline-color': '#888',
                'border-color': '#455A64',
                'background-color': '#859AA4'
              }
            },
            {
              selector: 'edge',
              style: {
                'width': 'data(lineWidth)',
                'line-color': '#859AA4',
                'opacity': '1',
                // 'target-arrow-color': 'data(lineColor)',
                // 'target-arrow-shape': 'triangle',
                // 'haystack-radius': '0.5',
                // 'opacity': '0.4',
                // 'line-color': '#bbb',
                // 'width': 'mapData(1, 0, 1, 1, 8)',
                // 'label': 'data(label)',
                'source-label': 'data(labelSourcePort)',
                'target-label': 'data(labelTargetPort)',
                'source-text-offset': 40,
                'target-text-offset': 40,
                // 'source-text-margin-y': -10,
                // 'target-text-margin-y': -10,
                'font-size': 10,
                'text-rotation': 'autorotate'
                // 'text-halign': 'right',
                // 'text-valign': 'top',
                // 'text-margin-y': -5
              }
            },
            {
              selector: '.link',
              style: {
                'curve-style': 'bezier',
                'control-point-step-size': 18,
                'overlay-padding': '2.5px'
                // 'target-arrow-shape': 'triangle',
                // 'target-arrow-color': '#859AA4',
                // 'arrow-scale': 0.7
              }
            },
            {
              selector: '.tunnel',
              style: {
                // 'source-endpoint': 'outside-to-node',
                // 'target-endpoint': 'outside-to-node',
                // 'source-arrow-shape': 'triangle-cross',
                // 'source-arrow-fill': 'hollow',
                // 'source-distance-from-node': '20px',
                // 'target-distance-from-node': '20px',
                'width': 20,
                'line-color': '#616161',
                'opacity': 0.25,
                'overlay-padding': '12px'
              }
            },
            {
              selector: '.tunnels',
              style: {
                // 'source-endpoint': 'outside-to-node',
                // 'target-endpoint': 'outside-to-node',
                // 'source-arrow-shape': 'triangle-cross',
                // 'source-arrow-fill': 'hollow',
                // 'source-distance-from-node': '20px',
                // 'target-distance-from-node': '20px',
                'width': 20,
                'line-color': '#616161',
                'opacity': 0.25,
                'overlay-padding': '12px'
              }
            },
            {
              selector: '.inner_tunnel',
              style: {
                // 'source-endpoint': 'outside-to-node',
                // 'target-endpoint': 'outside-to-node',
                // 'source-arrow-shape': 'triangle-cross',
                // 'source-arrow-fill': 'hollow',
                // 'source-distance-from-node': '20px',
                // 'target-distance-from-node': '20px',
                'curve-style': 'bezier',
                'width': 10,
                'target-arrow-color': '#00BCD4',
                'target-arrow-shape': 'triangle',
                'arrow-scale': 0.8,
                'line-color': '#00BCD4',
                'overlay-padding': '8px',
                'text-wrap': 'wrap',
                'opacity': 1
              }
            },
            {
              selector: '.lsp_wk',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-text-offset': 40,
                'target-text-offset': 40,
                'source-text-margin-y': '1px',
                'target-text-margin-y': '1px',
                'source-distance-from-node': '20px',
                'target-distance-from-node': '20px',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#00BCD4',
                'line-style': 'solid',
                'line-color': '#00BCD4',
                'text-wrap': 'wrap',
                'opacity': 1
              }
            },
            {
              selector: '.lsp_pt',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-text-offset': 40,
                'target-text-offset': 40,
                'source-text-margin-y': '1px',
                'target-text-margin-y': '1px',
                'source-distance-from-node': '20px',
                'target-distance-from-node': '20px',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#CDDC39',
                'line-style': 'solid',
                'line-color': '#CDDC39',
                'text-wrap': 'wrap',
                'opacity': 1
              }
            },
            {
              selector: ':selected',
              style: {
                'line-color': '#616161',
                'border-color': '#3d3d3d',
                'border-width': '2px',
                'background-color': '#2D3B41',
                'target-arrow-color': '#616161',
                'source-arrow-color': '#616161',
                'opacity': 1
              }
            },
            {
              selector: '.bar',
              style: {
                'width': '3',
                'curve-style': 'unbundled-bezier',
                'source-endpoint': '10px 20px',
                'target-endpoint': '-10px 20px',
                'line-style': 'dashed',
                'line-color': '#0F0',
                'target-arrow-shape': 'triangle',
                'target-arrow-color': '#f00',
                'opacity': 1
              }
            },
            {
              selector: '.node_icon_a',
              style: {
                'background-image': '../../static/image/node_icon_a.png'
              }
            },
            {
              selector: '.node_icon_b',
              style: {
                'background-image': '../../static/image/node_icon_b.png'
              }
            },
            {
              selector: '.node_icon_m',
              style: {
                'background-image': '../../static/image/node_icon_m.png'
              }
            },
            {
              selector: '.node_icon_v',
              style: {
                'background-image': '../../static/image/node_icon_v.png'
              }
            }
          ],
          layout: {
            name: 'cose-bilkent'
          }
        },
      )

      cy.viewport({ zoom: 1, pan: { x: 0, y: 0 } });
      cy.minZoom(0.1);
      cy.maxZoom(10);

      cy.panzoom({
        minZoom: 0.1,
        maxZoom: 10,
        animateOnFit: () => { return true },
        fitAnimationDuration: 300
      });

      cy.autopanOnDrag({});

      cy.navigator({
        container: false, // can be a HTML or jQuery element or jQuery selector
        viewLiveFramerate: 0, // set false to update graph pan only on drag end; set 0 to do it instantly; set a number (frames per second) to update not more than N times per second
        thumbnailEventFramerate: 30, // max thumbnail's updates per second triggered by graph updates
        thumbnailLiveFramerate: 2, // max thumbnail's updates per second. Set false to disable
        dblClickDelay: 200, // milliseconds
        removeCustomContainer: true, // destroy the container specified by user on plugin destroy
        rerenderDelay: 0 // ms to throttle rerender updates to the panzoom for performance
      });

      let self = this;
      cy.contextMenus({
        menuItems: [
          {
            id: 'label_format_name',
            content: 'Change label [ NAME ]',
            tooltipText: 'Change label [ NAME ]',
            // image: { src: 'remove.svg', width: 12, height: 12, x: 6, y: 4 },
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.name}`;
              }
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          },
          {
            id: 'label_format_ip',
            content: 'Change label [ IP ]',
            tooltipText: 'Change label [ IP ]',
            // image: { src: 'remove.svg', width: 12, height: 12, x: 6, y: 4 },
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.id}`;
              }
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          },
          {
            id: 'label_format_name_ip',
            content: 'Change label [ NAME (IP) ]',
            tooltipText: 'Change label [ NAME (IP) ]',
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.labelFormat = (item) => {
                return `${item.name} (${item.id})`;
              }
              self.onUpdate(store.state.elements);
            },
            hasTrailingDivider: true,
            coreAsWell: true
          },
          {
            id: 'show_port_label',
            content: 'Toggle port label',
            tooltipText: 'Toggle port label',
            selector: 'node, edge',
            onClickFunction: (event) => {
              self.showPortLabel = !self.showPortLabel;
              self.onUpdate(store.state.elements);
            },
            coreAsWell: true
          }
        ]
      });

      let makeTooltip = function(type, elem) {
        let text = '';
        let data = elem.data();
        if (type === 'node') {
          text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>ME ID</b>: ${data.me}<br><b>PRODUCT NAME</b>: ${data.product}<br>`;
        }

        if (type === 'edge') {
          if (elem.hasClass('tunnel')) {
            text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>CIR</b>: ${data.cir}<br><b>PIR</b>: ${data.pir}
            <br><b>[ WORKING LSP ]</b>
            <br><b>&nbsp;&nbsp;NAME</b>: ${data.wkLspName}
            <br><b>&nbsp;&nbsp;PATH TYPE</b>: ${data.wkLspPathType}
            <br><b>&nbsp;&nbsp;SRC NODE ID / NAME</b>: ${data.wkLspSrcNodeId} / ${data.wkLspSrcNodeName}
            <br><b>&nbsp;&nbsp;SRC IN/OUT LABEL</b>: ${data.wkLspSrcInLabel} / ${data.wkLspSrcOutLabel}
            <br><b>&nbsp;&nbsp;DST NODE ID / NAME </b>: ${data.wkLspDstNodeId} / ${data.wkLspDstNodeName}
            <br><b>&nbsp;&nbsp;DST IN/OUT LABEL</b>: ${data.wkLspDstInLabel} / ${data.wkLspDstOutLabel}
            <br><b>&nbsp;&nbsp;OAM - MEG NAME</b>: ${data.wkLspOamMegName} <b>MEP ID:</b> ${data.wkLspOamMepId} <b>RMEP ID:</b> ${data.wkLspOamRMepId} <b>CC INTERVAL:</b> ${data.wkLspOamCcInterval}
            <br><b>[ PROTECTION LSP ]</b>
            <br><b>&nbsp;&nbsp;NAME</b>: ${data.ptLspName}
            <br><b>&nbsp;&nbsp;PATH TYPE</b>: ${data.ptLspPathType}
            <br><b>&nbsp;&nbsp;SRC NODE ID / NAME</b>: ${data.ptLspSrcNodeId} / ${data.ptLspSrcNodeName}
            <br><b>&nbsp;&nbsp;SRC IN/OUT LABEL</b>: ${data.ptLspSrcInLabel} / ${data.ptLspSrcOutLabel}
            <br><b>&nbsp;&nbsp;DST NODE ID / NAME </b>: ${data.ptLspDstNodeId} / ${data.ptLspDstNodeName}
            <br><b>&nbsp;&nbsp;DST IN/OUT LABEL</b>: ${data.ptLspDstInLabel} / ${data.ptLspDstOutLabel}
            <br><b>&nbsp;&nbsp;OAM - MEG NAME</b>: ${data.ptLspOamMegName} <b>MEP ID:</b> ${data.ptLspOamMepId} <b>RMEP ID:</b> ${data.ptLspOamRMepId} <b>CC INTERVAL:</b> ${data.ptLspOamCcInterval}`;
          } else if (elem.hasClass('tunnels')) {
            text = `<b>TUNNELS (${data.count})</b><br><b>${data.source} - ${data.target}</b>`;
          } else if (elem.hasClass('inner_tunnel')) {
            text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>SRC NODE</b>: ${data.source}<br><b>DST NODE</b>: ${data.target}
            <br><b>SRC IN/OUT LABEL</b>: ${data.srcInLabel} / ${data.srcOutLabel}<br><b>DST IN/OUT LABEL</b>: ${data.dstInLabel} / ${data.dstOutLabel}`;
          } else if (elem.hasClass('lsp_wk') || elem.hasClass('lsp_pt')) {
            text = `<b>ID</b>: ${data.linkId}<br><b>NAME</b>: ${data.name}<br><b>SRC NODE</b>: ${data.source}<br><b>DST NODE</b>: ${data.target}
            <br><b>SRC PORT</b>: ${data.linkSrcPortId}<br><b>DST PORT</b>: ${data.linkDstPortId}<br><b>SRC IN/OUT LABEL</b>: ${data.srcInLabel} / ${data.srcOutLabel}
            <br><b>DST IN/OUT LABEL</b>: ${data.dstInLabel} / ${data.dstOutLabel}`;
          } else {
            text = `<b>ID</b>: ${data.id}<br><b>NAME</b>: ${data.name}<br><b>SRC NODE</b>: ${data.source}<br><b>DST NODE</b>: ${data.target}
            <br><b>SRC PORT</b>: ${data.sourcePort}<br><b>DST PORT</b>: ${data.targetPort}<br><b>SRC PRODUCT NAME</b>: ${data.sourceProduct}
            <br><b>DST PRODUCT NAME</b>: ${data.targetProduct}<br><b>BANDWIDTH</b>: ${data.bandwidth}<br><b>MAX BANDWIDTH</b>: ${data.maxBandwidth}`;
          }
        }
        return text;
      }
      let showTootip = function(elem, text) {
        elem.qtip({
          content: text,
          position: {
            my: 'top center',
            at: 'bottom center'
          },
          show: {
            cyBgOnly: true
          },
          style: {
            classes: 'qtip-bootstrap'
          }
        })
      }

      cy.on('click', 'node', (event) => {
        let elem = event.target;
        elem.qtip('api').destroy();
        showTootip(elem, makeTooltip('node', elem));
      });

      cy.on('click', 'edge', (event) => {
        let elem = event.target;
        elem.qtip('api').destroy();
        showTootip(elem, makeTooltip('edge', elem));
      });

      // cy.on('boxselect', function(event) {
      //   cy.elements().forEach(function(ele, i, eles) {
      //     if (ele.selected()) {
      //       // console.log(ele.id());
      //     }
      //   });
      // });

      // cy.on('select', (event) => {
      //   this.removeEdgesForPathDisplay();
      // });

      // cy.on('cxttap', (event) => { 
      //   if (this.edgesForPathDisplay.length > 0) {
      //     this.removeEdgesForPathDisplay();
      //     event.preventDefault();
      //   }
      // });

      cy.on('mouseup', (event) => {
        // if (event.target === this.cy) {
        //   console.log('asd');
          
        //   return;
        // }

        if (this.itemAnimationTimerId) {
          clearInterval(this.itemAnimationTimerId);
        }

        if (this.flagLspDisplayMode) {
          return;
        }

        setTimeout(() => {
          let elements = [];
          cy.elements().forEach((ele, i, eles) => {
            if (ele.selected()) {
              elements.push(ele.data());
            }
          });

          // if (this.edgesForPathDisplay.length > 0) {
          //   console.log(this.edgesForPathDisplay);
          //   let diff = elements.filter(element => this.edgesForPathDisplay.map(x => x.data.id).indexOf(element.id) === -1);
          //   console.log(diff);
          //   if (diff.length === 0) {
          //     return;
          //   } else {
          //     this.removeEdgesForPathDisplay();
          //   }
          // }

          let unique = new Set(elements.map((e) => e.type));
          if (unique.size > 0) {
            let type = '';
            if (unique.size > 1) {
              type = '*';
            } else {
              type = unique.values().next().value;
            }

            self.updatedBreadcrumb(type);
            EventBus.$emit('TOPOLOGY_ITEM_CHANGED', type, elements);
          } else {
            self.updatedBreadcrumb();
          }
        }, 100);
      });

      $('#topology-toolbar-link, #topology-toolbar-tunnel').on('click', function(event) {
        if ($(this).hasClass('disabled')) {
          event.stopPropagation();
          return;
        }

        let isChecked = ($(this).find('input:checked').length === 0);
        let target = $(this).find('input').val();

        self.cy.batch(() => {
          self.cy.nodes().connectedEdges('.' + target).forEach((ele, i, eles) => {
            if (isChecked) {
              ele.show();
            } else {
              ele.hide();
            }
          });
        })
      });

      $('#topology-lsp-wk-btn, #topology-lsp-pt-btn').on('click', function(event) {
        let isChecked = ($(this).find('input:checked').length === 0);
        let target = $(this).find('input').val();

        self.cy.batch(() => {
          if (target === 'lsp_wk') {
            self.cy.nodes().connectedEdges('.inner_tunnel').forEach((ele, i, eles) => {
              if (isChecked) {
                ele.show();
              } else {
                ele.hide();
              }
            });
          } 

          self.cy.nodes().connectedEdges('.' + target).forEach((ele, i, eles) => {
            if (isChecked) {
              ele.show();
            } else {
              ele.hide();
            }
          });
        })
      });
    },
    onUpdate(elements) {
      let self = this;

      let getNodePos = function() {
        let nodesPos = [];
        if (self.cy.nodes().length > 0) {
          self.cy.nodes().forEach((ele) => {
            nodesPos.push({
              id: ele.id(),
              pos: ele.position()
            })
          });
        }

        return nodesPos;
      }

      let createElementNode = function(node) {
        let nodeIcon;
        if (node.name === node.id) {
          nodeIcon = self.nodeIcons.get('V');
        } else {
          nodeIcon = self.nodeIcons.get(node.name.slice(0, 2));
        }
        let elemNode = {
          group: 'nodes',
          data: {
            id: node.id,
            name: node.name,
            type: 'node',
            label: self.labelFormat(node),
            me: node.extension.MeId,
            product: node.extension.ProductName,
            item: node
          },
          classes: (nodeIcon === undefined) ? '' : nodeIcon
        };

        return elemNode;
      }

      let createElementLink = function(link) {
        let labelSourcePort = `${/(slot=)\d+/.exec(link.srcPortId)[0].replace('slot=', '')}/${/(port=)\d+/.exec(link.srcPortId)[0].replace('port=', '')}`;
        let labelTargetPort = `${/(slot=)\d+/.exec(link.dstPortId)[0].replace('slot=', '')}/${/(port=)\d+/.exec(link.dstPortId)[0].replace('port=', '')}`;
        let elemLink = {
          group: 'edges',
          data: {
            id: link.id,
            name: link.name,
            source: link.srcNodeId,
            target: link.dstNodeId,
            type: 'link',
            label: `${labelSourcePort} / ${labelTargetPort}`, // link.name,
            labelSourcePort: (self.showPortLabel) ? labelSourcePort : '',
            labelTargetPort: (self.showPortLabel) ? labelTargetPort : '',
            sourcePort: link.srcPortId,
            targetPort: link.dstPortId,
            sourceProduct: link.srcProductName,
            targetProduct: link.dstProductName,
            bandwidth: link.bandwidth,
            maxBandwidth: link.maxBandwidth,
            srcSlotPort: labelSourcePort,
            dstSlotPort: labelTargetPort,
            item: link,
            lineWidth: 1 // (parseInt(link.bandwidth) <= 1000000) ? 0.5 : 1.5 //   
          },
          classes: 'link'
        };

        return elemLink;
      }

      let createElementTunnel = function(tunnel) {
        let elemTunnel = {
          group: 'edges',
          data: {
            id: tunnel.id,
            name: tunnel.name,
            source: tunnel.wkLsp.srcNodeId,
            target: tunnel.wkLsp.dstNodeId,
            cir: tunnel.qosInfo.cir,
            pir: tunnel.qosInfo.pir,
            wkLspName: tunnel.wkLsp.name,
            wkLspPathType: tunnel.wkLsp.pathType,
            wkLspSrcNodeId: tunnel.wkLsp.srcNodeId,
            wkLspSrcNodeName: tunnel.wkLsp.srcNodeName,
            wkLspSrcInLabel: tunnel.wkLsp.srcInLabel,
            wkLspSrcOutLabel: tunnel.wkLsp.srcOutLabel, // 
            wkLspDstNodeId: tunnel.wkLsp.dstNodeId,
            wkLspDstNodeName: tunnel.wkLsp.dstNodeName,
            wkLspDstInLabel: tunnel.wkLsp.dstInLabel,
            wkLspDstOutLabel: tunnel.wkLsp.dstOutLabel,
            wkLspOamMegName: $.isEmptyObject(tunnel.wkLsp.oamInfo) ? '' : tunnel.wkLsp.oamInfo.megName,
            wkLspOamMepId: $.isEmptyObject(tunnel.wkLsp.oamInfo) ? '' : tunnel.wkLsp.oamInfo.mepId,
            wkLspOamRMepId: $.isEmptyObject(tunnel.wkLsp.oamInfo) ? '' : tunnel.wkLsp.oamInfo.rMepId,
            wkLspOamCcInterval: $.isEmptyObject(tunnel.wkLsp.oamInfo) ? '' : tunnel.wkLsp.oamInfo.ccInterval,
            ptLspName: tunnel.ptLsps[0].name,
            ptLspPathType: tunnel.ptLsps[0].pathType,
            ptLspSrcNodeId: tunnel.ptLsps[0].srcNodeId,
            ptLspSrcNodeName: tunnel.ptLsps[0].srcNodeName,
            ptLspSrcInLabel: tunnel.ptLsps[0].srcInLabel,
            ptLspSrcOutLabel: tunnel.ptLsps[0].srcOutLabel, // 
            ptLspDstNodeId: tunnel.ptLsps[0].dstNodeId,
            ptLspDstNodeName: tunnel.ptLsps[0].dstNodeName,
            ptLspDstInLabel: tunnel.ptLsps[0].dstInLabel,
            ptLspDstOutLabel: tunnel.ptLsps[0].dstOutLabel,
            ptLspOamMegName: $.isEmptyObject(tunnel.ptLsps[0].oamInfo) ? '' : tunnel.ptLsps[0].oamInfo.megName,
            ptLspOamMepId: $.isEmptyObject(tunnel.ptLsps[0].oamInfo) ? '' : tunnel.ptLsps[0].oamInfo.mepId,
            ptLspOamRMepId: $.isEmptyObject(tunnel.ptLsps[0].oamInfo) ? '' : tunnel.ptLsps[0].oamInfo.rMepId,
            ptLspOamCcInterval: $.isEmptyObject(tunnel.ptLsps[0].oamInfo) ? '' : tunnel.ptLsps[0].oamInfo.ccInterval,
            item: tunnel
          },
          classes: 'tunnel'
        };

        return elemTunnel;
      }

      let createElementInnerPath = function(type, lsp) {
        let elemPath = [];
        let transitNodeCount = lsp.lspOuterPathInfo.lspOuterTransitInfo.length;
        for (let i = 0; i < lsp.lspOuterPathInfo.lspOuterTransitInfo.length; i++) {
          let transitNode = lsp.lspOuterPathInfo.lspOuterTransitInfo[i];
          let westTunnel = store.state.elements.tunnels.find((tunnel) => { return tunnel.id === transitNode.westTunnelId });
          let eastTunnel = store.state.elements.tunnels.find((tunnel) => { return tunnel.id === transitNode.eastTunnelId });
          if (westTunnel === undefined || eastTunnel === undefined) {
            console.log(`Not found tunnel(${transitNode.westTunnelId}:${westTunnel}/${transitNode.eastTunnelId}:${eastTunnel}`);
            continue;
          }

          if (i === 0) {
            let srcNodeId = westTunnel.extension.SrcNodeId;
            let dstNodeId = westTunnel.extension.DstNodeId;
            if (srcNodeId.indexOf('.') === -1) {
              let node = elements.nodes.find((node) => node.extension.MeId === srcNodeId);
              if (node) {
                srcNodeId = node.id;
              }
            }

            if (dstNodeId.indexOf('.') === -1) {
              let node = elements.nodes.find((node) => node.extension.MeId === dstNodeId);
              if (node) {
                dstNodeId = node.id;
              }
            }

            let elemWestTunnel = {
              group: 'edges',
              data: {
                id: westTunnel.id + '@',
                name: westTunnel.name,
                source: srcNodeId,
                target: dstNodeId,
                srcNodeId: srcNodeId,
                dstNodeId: dstNodeId,
                srcInLabel: lsp.srcInLabel,
                srcOutLabel: lsp.srcOutLabel,
                dstInLabel: transitNode.westInLabel,
                dstOutLabel: transitNode.westOutLabel,
                labelSourcePort: ` IN: ${lsp.srcInLabel} \nOUT: ${lsp.srcOutLabel}`,
                labelTargetPort: ` IN: ${transitNode.westInLabel} \nOUT: ${transitNode.westOutLabel}`,
                item: westTunnel
              },
              classes: 'inner_tunnel'
            }
            elemPath.push(elemWestTunnel);
          }

          let srcNodeId = eastTunnel.extension.SrcNodeId;
          let dstNodeId = eastTunnel.extension.DstNodeId;
          if (srcNodeId.indexOf('.') === -1) {
            let node = elements.nodes.find((node) => node.extension.MeId === srcNodeId);
            if (node) {
              srcNodeId = node.id;
            }
          }

          if (dstNodeId.indexOf('.') === -1) {
            let node = elements.nodes.find((node) => node.extension.MeId === dstNodeId);
            if (node) {
              dstNodeId = node.id;
            }
          }

          console.log(transitNode);
          let dstInLabel = (i + 1 < transitNodeCount) ? lsp.lspOuterPathInfo.lspOuterTransitInfo[i + 1].westInLabel : lsp.dstInLabel;
          let dstOutLabel = (i + 1 < transitNodeCount) ? lsp.lspOuterPathInfo.lspOuterTransitInfo[i + 1].westOutLabel : lsp.dstOutLabel;
          let elemEastTunnel = {
            group: 'edges',
            data: {
              id: eastTunnel.id + '@',
              name: eastTunnel.name,
              source: srcNodeId,
              target: dstNodeId,
              srcNodeId: srcNodeId,
              dstNodeId: dstNodeId,
              srcInLabel: transitNode.eastInLabel,
              srcOutLabel: transitNode.eastOutLabel,
              dstInLabel: dstInLabel,
              dstOutLabel: dstOutLabel,
              labelSourcePort: ` IN: ${transitNode.eastInLabel} \nOUT: ${transitNode.eastOutLabel}`,
              labelTargetPort: ` IN: ${dstInLabel} \nOUT: ${dstOutLabel}`,
              item: eastTunnel
            },
            classes: 'inner_tunnel'
          }
          elemPath.push(elemEastTunnel);
        }

        console.log(elemPath);
        return elemPath;
      }

      let createElementOuterPath = function(type, lsp) {
        let elemPath = [];
        if (lsp.lspNniPathInfo.hasOwnProperty('lspNniTransitInfo') === false) {
          let elemLsp = {
            data: {
              id: lsp.lspNniPathInfo.srcLinkId + '@',
              name: lsp.name,
              source: lsp.lspNniPathInfo.srcLinkSrcNodeId,
              target: lsp.lspNniPathInfo.srcLinkDstNodeId,
              linkId: lsp.lspNniPathInfo.srcLinkId,
              linkSrcNodeId: lsp.lspNniPathInfo.srcLinkSrcNodeId,
              linkDstNodeId: lsp.lspNniPathInfo.srcLinkDstNodeId,
              linkSrcPortId: lsp.lspNniPathInfo.srcLinkSrcPortId,
              linkDstPortId: lsp.lspNniPathInfo.srcLinkDstPortId,
              srcInLabel: lsp.srcInLabel,
              srcOutLabel: lsp.srcOutLabel,
              dstInLabel: lsp.dstInLabel,
              dstOutLabel: lsp.dstOutLabel,
              labelSourcePort: ` IN: ${lsp.srcInLabel} \nOUT: ${lsp.srcOutLabel}`,
              labelTargetPort: ` IN: ${lsp.dstInLabel} \nOUT: ${lsp.dstOutLabel}`,
              item: lsp
            }
          };

          elemPath.push(elemLsp);
        } else {
          let transitNodeCount = lsp.lspNniPathInfo.lspNniTransitInfo.length;
          for (let i = 0; i < lsp.lspNniPathInfo.lspNniTransitInfo.length; i++) {
            let transitNode = lsp.lspNniPathInfo.lspNniTransitInfo[i];
            let westLink = store.state.elements.links.find((link) => { return link.id === transitNode.westLinkId });
            let eastLink = store.state.elements.links.find((link) => { return link.id === transitNode.eastLinkId });
            if (westLink === undefined || eastLink === undefined) {
              console.log(`Not found tunnel (${transitNode.westLinkId}:${westLink} / ${transitNode.eastLinkId}:${eastLink})`)
              continue;
            }

            if (i === 0) {
              // west
              let elemWestLsp = {
                data: {
                  id: transitNode.westLinkId + '@@',
                  name: lsp.name,
                  source: westLink.srcNodeId,
                  target: westLink.dstNodeId,
                  linkId: westLink.id,
                  linkSrcNodeId: westLink.srcNodeId,
                  linkDstNodeId: westLink.dstNodeId,
                  linkSrcPortId: westLink.srcPortId,
                  linkDstPortId: westLink.dstPortId,
                  srcInLabel: lsp.srcInLabel,
                  srcOutLabel: lsp.srcOutLabel,
                  dstInLabel: transitNode.westInLabel,
                  dstOutLabel: transitNode.westOutLabel,
                  labelSourcePort: ` IN: ${lsp.srcInLabel} \nOUT: ${lsp.srcOutLabel}`,
                  labelTargetPort: ` IN: ${transitNode.westInLabel} \nOUT: ${transitNode.westOutLabel}`,
                  item: lsp
                }
              }

              elemPath.push(elemWestLsp);
            }

            // east
            let dstInLabel = (i + 1 < transitNodeCount) ? lsp.lspNniPathInfo.lspNniTransitInfo[i + 1].westInLabel : lsp.dstInLabel;
            let dstOutLabel = (i + 1 < transitNodeCount) ? lsp.lspNniPathInfo.lspNniTransitInfo[i + 1].westOutLabel : lsp.dstOutLabel;
            let elemEastLsp = {
              data: {
                id: transitNode.eastLinkId + '@@',
                name: lsp.name,
                source: eastLink.srcNodeId,
                target: eastLink.dstNodeId,
                linkId: eastLink.id,
                linkSrcNodeId: eastLink.srcNodeId,
                linkDstNodeId: eastLink.dstNodeId,
                linkSrcPortId: eastLink.srcPortId,
                linkDstPortId: eastLink.dstPortId,
                srcInLabel: transitNode.eastInLabel,
                srcOutLabel: transitNode.eastOutLabel,
                dstInLabel: dstInLabel,
                dstOutLabel: dstOutLabel,
                labelSourcePort: ` IN: ${transitNode.eastInLabel} \nOUT: ${transitNode.eastOutLabel}`,
                labelTargetPort: ` IN: ${dstInLabel} \nOUT: ${dstOutLabel}`,
                item: lsp
              }
            }

            elemPath.push(elemEastLsp);
          }
        }

        elemPath.forEach(function(element) {
          Object.assign(element, { group: 'edges', classes: `lsp_${type}` });
        });

        return elemPath;
      }

      let createElementPath = function(type, lsp) {
        let elemPath = [];

        if (lsp.pathType === 'Inner') {
          elemPath = createElementInnerPath(type, lsp);
        }

        if (lsp.pathType === 'Outer') {
          elemPath = createElementOuterPath(type, lsp);
        }

        return elemPath;
      }

      let createElementsForTopologyDisplay = function(elements) {
        let eles = [];
        let nodesPos = getNodePos();
        for (let node of elements.nodes) {
          let elemNode = createElementNode(node);
          let oldNodePos = nodesPos.find((nodePos) => {
            return nodePos.id === node.id;
          });

          if (oldNodePos) {
            Object.assign(elemNode, { position: oldNodePos.pos });
          }
          eles.push(elemNode);
        }

        for (let link of elements.links) {
          let nodesById = elements.nodes.filter((node) => {
            return (node.id === link.srcNodeId) || (node.id === link.dstNodeId);
          });

          if (nodesById.length >= 2) {
            let elemLink = createElementLink(link);
            eles.push(elemLink);
          }
        }

        let tunnelsBySection = [];
        for (let tunnel of elements.tunnels) {
          let srcNodeId = tunnel.extension.SrcNodeId;
          let dstNodeId = tunnel.extension.DstNodeId;

          if (srcNodeId.indexOf('.') === -1) {
            let node = elements.nodes.find((node) => node.extension.MeId === srcNodeId);
            if (node) {
              srcNodeId = node.id;
            }
          } else {
            if (elements.nodes.find((node) => node.id === srcNodeId) === undefined) {
              console.log(`Not found ${srcNodeId}(LSRID) has node`);
              continue;
            }
          }

          if (dstNodeId.indexOf('.') === -1) {
            let node = elements.nodes.find((node) => node.extension.MeId === dstNodeId);
            if (node) {
              dstNodeId = node.id;
            }
          } else {
            if (elements.nodes.find((node) => node.id === dstNodeId) === undefined) {
              console.log(`Not found ${dstNodeId}(LSRID) has node`);
              continue;
            }
          }

          if (srcNodeId === undefined || dstNodeId === undefined) {
            console.log(`Not found ME ID (srcNodeId='${tunnel.extension.SrcNodeId} / dstNodeId=${tunnel.extension.DstNodeId})`);
            continue;
          }

          let origin = `${srcNodeId}^${dstNodeId}`;
          let reverse = `${dstNodeId}^${srcNodeId}`;
          let tunnels = tunnelsBySection.find((tunnels) => {
            return (tunnels.id === origin || tunnels.id === reverse);
          });

          if (tunnels !== undefined) {
            tunnels.childList.push(tunnel);
          } else {
            tunnelsBySection.push({
              id: origin,
              source: srcNodeId,
              target: dstNodeId,
              childList: [tunnel]
            });
          }
        }

        tunnelsBySection.forEach((tunnels, index) => {
          eles.push({
            group: 'edges',
            data: {
              id: 'tunnels' + index,
              name: '',
              type: 'tunnels',
              count: tunnels.childList.length,
              tunnels: tunnels.childList,
              source: tunnels.source,
              target: tunnels.target,
              item: tunnels
            },
            classes: 'tunnels'
          })
        });

        return eles;
      }

      let createElementsForTunnelDisplay = function(elements, tunnel) {
        let eles = [];
        let nodesPos = getNodePos();
        for (let node of elements.nodes) {
          let elemNode = createElementNode(node);
          let oldNodePos = nodesPos.find((nodePos) => {
            return nodePos.id === node.id;
          });

          if (oldNodePos) {
            Object.assign(elemNode, { position: oldNodePos.pos });
          }
          eles.push(elemNode);
        }

        let newTunnel = createElementTunnel(tunnel);
        eles.push(newTunnel);

        let path = createElementPath('wk', tunnel.wkLsp);
        path = path.concat(createElementPath('pt', tunnel.ptLsps[0]));
        path.forEach(function(lsp) {
          for (let link of store.state.elements.links) {
            let nodesById = store.state.elements.nodes.filter((node) => {
              return (node.id === link.srcNodeId) || (node.id === link.dstNodeId);
            });
            let linkId = lsp.data.id;
            if (linkId.replace(/@/gi, '') !== link.id) {
              continue;
            }

            if (nodesById.length < 2) {
              continue;
            }

            let newLink = createElementLink(link);
            eles.push(newLink);
          }
        }, this);

        eles = eles.concat(path);
        return eles;
      }

      let cy = this.cy;
      if (this.flagLspDisplayMode) {
        HTTP.get('requestTunnel.json').then((response) => {
          let tunnel = response.data.content.requestTunnelInfo.response;
          let eles = createElementsForTunnelDisplay(elements, tunnel);

          cy.startBatch();
          cy.elements().remove();
          cy.add(eles);
          cy.elements().qtip({ content: () => { } });
          cy.endBatch();
  
          $('#topology-toolbar > label').addClass('disabled');
          $('#topology-toolbar > label').removeClass('active');
          $('#topology-randomize').prop('disabled', true);
          $('#topology-lsp-toolbar > label').addClass('active');
          $('#topology-lsp-toolbar').css('visibility', 'visible');
        }).catch((err) => {
          console.log(err);
        });
      } else {
        let eles = createElementsForTopologyDisplay(elements);
        cy.startBatch();
        cy.elements().remove();
        cy.add(eles);

        if (!this.initLoad) {
          cy.center(this.cy.nodes());
          cy.layout(this.topologyLayout).run();
          this.initLoad = true;
        }

        $('#topology-toolbar > label').removeClass('disabled');
        $('#topology-toolbar > label').addClass('active');
        $('#topology-randomize').prop('disabled', false);
        $('#topology-randomize').on('click', function() { self.cy.layout(self.topologyLayout).run(); });
        $('#topology-lsp-toolbar').css('visibility', 'hidden');

        // register empty qtip for using qtip api
        cy.elements().qtip({ content: () => { } });
        cy.endBatch();
      }
    },
    updatedBreadcrumb(type) {
      let $ol = $('.breadcrumb');
      $ol.empty();
      $ol.append('<li id="li-topology"><a href="#">Topology</a></li>&nbsp;');

      switch (type) {
        case 'node':
          $ol.append(`<li id="li-node" class="active">Node</li>`);
          break;
        case 'link':
          $ol.append(`<li id="li-link" class="active">Link</li>`);
          break;
        case 'tunnels':
          $ol.append(`<li id="li-tunnels" class="active">Tunnel</li>`);
          break;
        case 'lsp':
          // $ol.append('<li id="li-tunnel"><a href="#">Tunnel</a></li>');
          $ol.append('<li id="li-lsp" class="active">Tunnel - LSP</li>');
          break;
        case '*':
          $ol.append(`<li id="li-all" class="active">Compound items</li>`);
          break;
        default:
          $ol.append('<li id="li-empty"></li>');
      }

      let self = this;
      $('#li-topology > a').on('click', function() {
        self.updatedBreadcrumb();
        self.selectedTunnelId = '';
        self.flagLspDisplayMode = false;
        self.onUpdate(store.state.elements);
      });
    }
  }
}
</script>

<style>
@import '../../node_modules/cytoscape-panzoom/cytoscape.js-panzoom.css';
@import '../../node_modules/cytoscape-panzoom/font-awesome-4.0.3/css/font-awesome.min.css';
@import '../../node_modules/cytoscape-context-menus/cytoscape-context-menus.css';
@import '../../node_modules/cytoscape-navigator/cytoscape.js-navigator.css';
@import '../../node_modules/qtip2/dist/jquery.qtip.css';

#cy {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0px;
  left: 0px;
}

#topology-breadcrumb {
  position: absolute;
  margin: 0px;
  padding: 0px;
  left: 50px;
  font-size: 12px;
  z-index: 1;
}

.breadcrumb {
  background-color: transparent;
}

#topology-randomize {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 105px;
}

#topology-toolbar {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 1px;
}

#topology-lsp-toolbar {
  position: absolute;
  z-index: 1;
  top: 35px;
  right: 1px;
  visibility: hidden;
}

#topology-lsp-wk-btn,
#topology-lsp-pt-btn {
  color: #7b7b7b; 
  background-color: #FFFFFF; 
  border-color: #ccc; 
}

#topology-lsp-wk-btn:focus, 
#topology-lsp-wk-btn:active, 
#topology-lsp-wk-btn.active { 
  color: #fff; 
  background-color: #00BCD4; 
  border-color: #ccc; 
}

#topology-lsp-pt-btn:focus, 
#topology-lsp-pt-btn:active, 
#topology-lsp-pt-btn.active { 
  color: #fff; 
  background-color: #CDDC39; 
  border-color: #ccc; 
}


/* #topology-chosen {
  position: absolute;
  z-index: 1;
  top: 1px;
  right: 1px;
} */

.cy-panzoom {
  z-index: 1;
}

.cytoscape-navigator {
  position: fixed;
  border: 1px solid #e5e5e5;
  background: #fff;
  width: 200px;
  height: 200px;
  bottom: 0;
  right: 0;
  overflow: hidden;
  z-index: 1;
}

.btn.focus,
.btn:active.focus,
.btn.active.focus {
  outline: none;
}

.btn.focus {
  border: 1px solid #ccc;
  background: #fff;
}

.qtip {
  max-width: 1000px;
  font-size: 10px;
  font-family: 'helvetica neue, helvetica, arial, sans-serif';
}


/* .chosen-select-deselect {
  width: 400px;
}

.chosen-container-single .chosen-single {
  height: 29px;
  line-height: 29px;
  font-size: 12px;
} */
</style>


